.footer{padding:2rem 0;background:#000;width:100%;bottom:0}.footer-container{display:flex;flex-wrap:wrap}.footer-title{color:#fff;margin-bottom:.2rem;display:flex;justify-content:space-between}.footer-content{color:#acacac;display:flex;justify-content:space-between;align-items:center}.footer-content .footer-content-left,.footer-content .footer-content-right{display:flex}.footer-content .footer-content-right .footer-link{border-right:1px solid #a7a7a7;display:flex;justify-content:center;align-items:center;height:50%;padding:0 .5rem}.footer-content .footer-content-right .footer-link:nth-last-child(1){border-right:0;padding-right:0}.footer-content .footer-content-right .footer-link a{color:#a7a7a7}.footer-col{width:100%}.footer-contact{cursor:pointer;background:linear-gradient(to bottom, #d3b719 10%, #ffe02e 20%, #d3b719);width:2.5rem;height:2.5rem;display:flex;align-items:center;justify-content:center;color:#000;font-size:1rem;margin-right:1rem;border-radius:10px}.footer-contact span{font-size:2rem;display:flex}.right-icon{margin-left:-0.2rem;margin-right:0.5rem}.footer-bank{flex-direction:row;flex-wrap:wrap;margin-bottom:1rem;border-bottom:1px solid #333}.footer-bank a{margin-right:1rem;margin-bottom:1rem;-webkit-filter:grayscale(30%);filter:grayscale(30%)}.footer-bank a:nth-last-child(1){margin-right:0}.footer-bank a img{width:3.4rem;height:3.4rem;border-radius:10px}.why-us{padding-bottom:1rem}@media screen and (max-width: 768px){.footer{padding:2rem 1rem 5rem 1rem}.footer-col{width:100%}.footer-container{flex-wrap:wrap}.footer-title{margin-bottom:1rem}.footer-content{flex-wrap:wrap}.footer-content .footer-contact{margin-bottom:.5rem}.quick-link{display:none !important}.why-us,.contact-us,.footer-nav,.footer-bank{padding-bottom:1rem}.footer-bank a{margin-right:0}.footer-bank a img{width:3rem;height:3rem;margin-right:.5rem}.footer-content-right{flex-wrap:wrap}}

.mobile-nav-bg{display:flex;position:absolute;left:-100%;z-index:2;width:100%;height:100%;background:rgba(0,0,0,0.7);transition:0.3s ease all;transform:translateX(0)}.mobile-nav-bg.active{transform:translateX(100%)}.mobile-nav{width:50%;z-index:3;display:flex;flex-direction:column;padding:1rem;background:#000}.mobile-nav a{color:gold;padding:5px 10px;margin-bottom:0.5rem;display:flex;align-items:center;white-space:nowrap}.mobile-nav a:hover{color:#fff;text-decoration:none}.mobile-nav a svg{margin-right:5px;min-width:1rem;min-height:1rem}.mobile-mask{width:50%;height:100%;z-index:3}@keyframes sidenav{0%{transform:translateX(-100%)}100%{transform:translateX(0%)}}

.mobile-footer{border-top:1px solid gold;background:#000;position:fixed;bottom:0;width:100%;display:none;justify-content:space-between;align-items:center;z-index:3;padding:1rem 15px}.mobile-footer a{display:flex;flex-direction:column;align-items:center;color:gold;text-decoration:none;font-size:0.8rem;padding:0 0.8rem;white-space:nowrap;max-width:20%}.mobile-footer svg{min-width:1.5rem;min-height:1.5rem}.mobile-footer button{min-width:5rem;min-height:5rem;border-radius:50%;font-size:0.8rem;margin-top:-50px;border:none;outline:none;background:repeating-linear-gradient(75grad, rgba(0,0,0,0) 25%, rgba(255,255,255,0.6) 26%, rgba(0,0,0,0) 50%, rgba(0,0,0,0) 100%),gold;font-weight:bold;animation:joinbtn 2s linear infinite;box-shadow:0 0 0 0px}@keyframes joinbtn{0%{box-shadow:0 0 0 0px rgba(252,246,186,0.5),0 0 0 0px rgba(179,135,40,0.5);background-position:0px 0px}100%{box-shadow:0 0 0 15px rgba(179,135,40,0),0 0 0 0px rgba(252,246,186,0);background-position:500px 0px}}@media screen and (max-width: 768px){.mobile-footer{display:flex}}

